@value borderColor, backShade, secondaryFontColor, primaryFontColor from 'SharedStyles/globalStyles.module.css';

/**
 * FeedBox styles
 */
.container {
  margin-top: 20px;
  border: 1px solid borderColor;

}
.container:first-child { margin-top: 0px; }

.header {
  border-bottom: 1px solid borderColor;
  height: 30px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
  background-color: backShade;
}

.title {
  padding-left: 10px;
  font-size: 13px;
  font-weight: 700;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.sortList .sort {
  color: secondaryFontColor;
  display: inline-block;
  height: 30px;
  line-height: 30px;
  font-size: 10px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  padding: 0px 10px;
  cursor: pointer;
  user-select: none;

}
.posts {
  display: flex;
  flex-direction: column;
}

.loading {
  font-size: 12px;
  letter-spacing: 0.5px;
  color: secondaryFontColor;
  padding: 20px 0px;
  text-align: center;
}

.sortList .sortActive { color: primaryFontColor; }